.carousel {
    background-image: url("/assets/images/windwaker/water-bg-2.jpg");
    width: 100%;
    background-attachment: fixed;
    padding: 30px;
    position: relative;
    box-sizing: border-box;
    text-align: center;

 }
 
 .carousel__container {
    white-space: nowrap;
    margin: 70px 0px;
    padding-bottom: 10px; 
 }
 .carousel-item {
    background-color:$zeldaRed;
    width: 200px;
    height: 250px;
    border-radius: 20px;
    overflow: hidden;
    margin-right: 10px;
    display: inline-block;
    cursor: pointer;
    transition: 450ms all;
    transform-origin: center left;
    position: relative;
 }
 .carousel-item:hover ~ .carousel-item {
    transform: translate3d(100px, 0, 0);
 }
 .carousel__container:hover .carousel-item {
    opacity: 0.3;
 }
 .carousel__container:hover .carousel-item:hover {
    transform: scale(1.5);
    opacity: 1;
 }
 .carousel-item__img {
    width: 200px;
    height: 250px;
    object-fit: cover;
 }
 .carousel-item__details {
    background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 100%);
    font-size: 10px;
    color: white;
    opacity: 0;
    transition: 450ms opacity;
    padding: 10px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    white-space: normal;
    display: flex;
    flex-flow: row wrap;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
 }
 .carousel-item__details:hover {
    opacity: 1;
 }
 .carousel-item__details--title {
   font-size: 15px;
 }
 
 .carousel-item__details--icons {
   color: #caa5f1;
 }

 


  