$display: grid;
$templatecolumn: repeat(auto-fit, minmax(260px,1fr));
$templaterow: repeat(auto-fit, minmax(100px,1fr));
$gap: 20px;
$content: space-around;
$center: center;
$start: start;


.bodycontent{
    display: $display;
    grid-template-columns: $templatecolumn;
    grid-template-rows: $templaterow;
    grid-gap: $gap;
    justify-content: $content;
    align-items: $start;
    justify-items: $center;
}